<script>
  import JSZip from "jszip";
  import { saveAs } from "file-saver";
  import "bulma/css/bulma.css";

  import { mode } from "./Stores/dataStore";

  import Navbar from "./Navbar.svelte";
  import Palette from "./Palette.svelte";
  import Canvas from "./Canvas.svelte";
  /* import Generate from "./Generate.svelte"; */
  import TileDataModal from "./TileSettings/TileDataModal.svelte";

  import { cleanUpTiles } from "./tileManagement";
  import tiles from "../data/img/index";

  let selectedIndex;

  let config = null;

  const downloadFiles = async (tileData, neighborData) => {
    const zip = new JSZip();

    const dataToDownload = { tiles: tileData, neighbors: neighborData };

    var blob = new Blob([JSON.stringify(dataToDownload)], {
      type: "text/plain;charset=utf-8",
    });

    console.log("data", dataToDownload);
    saveAs(blob, "tiledata.json");
  };

  const handleSubmit = ({ detail: tileData }) => {
    console.log(tileData);
    const { grid } = tileData;
    const relations = cleanUpTiles(tiles, grid);

    config = relations;
    console.log(relations);
  };
</script>

<div class="maincontainer">
  <Navbar />

  <div class="content">
    <div class="columns" style="height: 100%">
      <div class="column is-narrow" style="height: 100%">
        <Palette bind:selectedIndex />
      </div>
      <div class="column" style="height: 100%">
        <!-- {#if $mode === "create"} -->
        <Canvas
          {selectedIndex}
          on:submitGrid={handleSubmit}
          on:changedGrid={() => {
            config = null;
          }}
          on:downloadConfig={() => {
            downloadFiles(tiles, config);
          }}
        />
      </div>
    </div>
  </div>
</div>
<TileDataModal />

<style>
  .maincontainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  .content {
    flex-grow: 1;
    min-height: 200px;
  }

  .canvasContainer {
    display: flex;
    flex-direction: column;
  }
</style>
